.GoogleMap {
  position: relative;
  float: right;
  width: 100%;
  padding:0px 10px;
  
}
.GoogleMap .button {
  background: none padding-box rgb(255, 255, 255);
    display: table-cell;
    border: 0px;
    margin: 0px;
    padding: 0px 17px;
    text-transform: none;
    appearance: none;
    position: relative;
    cursor: pointer;
    user-select: none;
    direction: ltr;
    overflow: hidden;
    text-align: center;
    height: 40px;
    vertical-align: middle;
    color: rgb(86, 86, 86);
    font-family: Roboto, Arial, sans-serif;
    font-size: 18px;
    border-bottom-right-radius: 2px;
    border-top-right-radius: 2px;
    box-shadow: rgb(0 0 0 / 30%) 0px 1px 4px -1px;
    min-width: 56px;
    top:10px;
    left:176px;
}
.GoogleMap .button:hover {background-color:rgb(206, 192, 192);}

.GoogleMap .Intra_Title{
  margin-left: 0px;
  height:auto;
}